import React from 'react'

import Layout from '../components/layout'
import SEO from '../components/seo'
import DynamicImage from '../components/dynamicimage'
import { StyledIFrame } from '../components/StyledIFrame'
import { StyledA } from '../components/StyledLink'
import { Card, CardHeader, CardBody, CardFooter } from '../components/Card'
import { Grid } from '../components/grid'
import { CenteredBlock } from '../components/CenteredBlock'
import { projects } from '../data/projects'

interface IProfileCardProps {
  image?: string
  video?: string
  title: string
  body: (string | { url: string; text: string })[]
  tags?: string[]
}

const ProjectCard = ({
  image,
  video,
  title,
  body,
  tags,
}: IProfileCardProps) => (
  <Card>
    <CardHeader>
      {image && <DynamicImage path={image} />}
      {video && (
        <StyledIFrame
          width="960"
          height="340"
          src={video}
          frameborder="0"
          allow="accelerometer; autoplay; encrypted-media; gyroscope; picture-in-picture"
          allowfullscreen
        ></StyledIFrame>
      )}
    </CardHeader>
    <CardBody>
      <h2>{title}</h2>
      <p>
        {body.map(string => {
          if (typeof string !== 'string' && string.url) {
            return <StyledA href={string.url}>{string.text}</StyledA>
          }
          return string
        })}
      </p>
    </CardBody>
    <CardFooter>
      <p>Tags: {tags && tags.map(tag => <span>{tag}</span>)}</p>
    </CardFooter>
  </Card>
)

const Projects = () => (
  <Layout>
    <CenteredBlock>
      <SEO title="My Projects so far" />
      <h1>Hey! These are some of my projects</h1>
      <p>
        In chronological order. These are some projects I worked on that I am
        proud of and / or got me some decent grades. Not everything in here has
        to do with programming. I also included some projects I made using Adobe
        Illustrator and After Effects.
      </p>
    </CenteredBlock>
    <Grid maxWidth="100vw" columns="repeat(auto-fill, minmax(32rem, 1fr))">
      {projects.map(({ title, image, video, body, tags }, i) => (
        <ProjectCard
          title={title}
          image={image}
          video={video}
          body={body}
          tags={tags}
          key={i}
        />
      ))}
    </Grid>
  </Layout>
)

export default Projects
